@use 'base/variables' as v;
@use 'base/mixins' as m;
.modal{
    position: absolute;
    background-color: rgb(0 0 0 / .8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .formulario{
        max-width: 60rem;
        margin: 0 auto;
        background-color: v.$blanco;
        padding: 2rem;
        transform: translateY(-30rem);
        transition:  all .5s ease-in;
        opacity: 0;
        border-radius: 1rem;
        input[type="text"]{
            border: 1px solid v.$gris;
            border-radius: .5rem;
        }
        &.animar{
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        &.cerrar{
            transform: translateY(-200%);
            opacity: 0;
        }
        legend{
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
        }
        .opciones{
            display: flex;
            justify-content: space-between;
            align-items: center;
            .submit-nueva-tarea{
                @include m.boton(v.$indigo, v.$blanco);
              padding: 1.5rem 2rem;
               }
               .cerrar-modal{
                   @include m.boton(v.$naranja, v.$blanco);
               }
        }
    }
}