@use 'base/mixins' as m;
@use 'base/variables' as v;
.formulario{
    margin-top: 5rem;
    .campo{
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
    }
    label{
        flex: 0 0  10rem;
    }
    input:not([type="submit"]){
        flex: 1;
        padding: 1rem;
        border: none;
        background-color: lighten($color: v.$gris, $amount: 40%);
    }
}